.message-popup {
	display: none;

	align-items: center;
	justify-content: center;

	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;

	background-color: var(--message-popup-bg);

	&.show {
		display: flex;
	}

	h1 {
		font-size: 1.5rem;
		text-transform: uppercase;
	}
}
